import { useEffect, useMemo, useState } from 'react';
import { Helmet } from "react-helmet";
import { GET_LOCAL } from '../../utils';


const HeaderScript = ({ authorized }) => {
   const [addScript, setAddScript] = useState(false);
   
   const { emailId, cookieConsent } = useMemo(() => ({
      emailId: authorized?.userData?.user_data?.user?.emailId ?? "",
      cookieConsent: authorized?.cookieConsent ?? false
   }), [authorized])

   useEffect(() => {
      const show = cookieConsent && !GET_LOCAL('isAdmin') && window.location.host.includes('app.') && emailId && !(emailId?.includes('taggbox.com'));
      setAddScript(show);
   }, [emailId, cookieConsent])

   return addScript ? <Helmet>
      <script>{
         `if(clarity){
                  clarity("set", "userEmail", "${emailId}");
                  clarity("set", "type", "app");
               }`}</script>
   </Helmet> : null
}

export default HeaderScript